import {
    CognitoUserPool,
    CognitoUserAttribute
} from 'amazon-cognito-identity-js'
import { Config, CognitoIdentityCredentials } from 'aws-sdk'

const signUp = (user) => {
    // Amazon Cognito 認証情報プロバイダーを初期化します
    Config.region = 'ap-northeast-1'; // リージョン
    Config.credentials = new CognitoIdentityCredentials({
        IdentityPoolId: 'ap-northeast-1:59eae5e8-5505-4c6f-8e3c-d59dfa8d3ee0',
    });
    const poolData = {
        UserPoolId: 'ap-northeast-1_tVozip5AO', //ユーザープールのID
        ClientId: 'fli418p183r1o5879rmm2l949' //クライアントアプリの設定上のID
    };

    //ユーザープール＋クライアントアプリの情報を格納
    const userPool = new CognitoUserPool(poolData);
    const attributeList = [
        new CognitoUserAttribute({
            Name: "family_name",
            Value: user.familyName
        }),
        new CognitoUserAttribute({
            Name: "given_name",
            Value: user.givenName
        })
    ];

    return new Promise((resolve, reject) => {
        userPool.signUp(user.email, user.password, attributeList, null, (err, result) => {
            if (err) {
                reject(err)
            } else {
                resolve(result)
            }
        })
    })
}

const saveAccessLog = async (log) => {
    const options = {
        method: "POST",
        mode: "cors",
        headers: {
            "Content-Type": "application/json"
        },
        referrerPolicy: "no-referrer",
        body: JSON.stringify(log)
    }

    // カテゴリの取得
    try {
        const response = await fetch("https://36wml4aq6i.execute-api.ap-northeast-1.amazonaws.com/default/ts-lambda-600013", options)
        return response.text()
    } catch (error) {
        return error
    }
}

export { signUp, saveAccessLog }
